'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import { Menu, X, Sun, Moon } from 'lucide-react';
import ThemeToggle from './themeToggle';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const [isDark, setIsDark] = useState(false);

  const navItems = [
    { href: '/', label: 'Inicio' },
    { href: '/amortizacion', label: 'Amortización' },
    { href: '/bonificacion', label: 'Bonificación' },
  ];

  // Detectar tema al cargar
  useEffect(() => {
    if (
      localStorage.theme === 'dark' ||
      (!('theme' in localStorage) &&
        window.matchMedia('(prefers-color-scheme: dark)').matches)
    ) {
      document.documentElement.classList.add('dark');
      setIsDark(true);
    } else {
      document.documentElement.classList.remove('dark');
      setIsDark(false);
    }
  }, []);

  // Cambiar tema y guardar preferencia
  const toggleTheme = () => {
    if (document.documentElement.classList.contains('dark')) {
      document.documentElement.classList.remove('dark');
      localStorage.theme = 'light';
      setIsDark(false);
    } else {
      document.documentElement.classList.add('dark');
      localStorage.theme = 'dark';
      setIsDark(true);
    }
  };

  return (
    <header className="sticky top-0 z-20 mx-auto px-4 md:px-12 flex w-full max-w-6xl items-center justify-between gap-4 py-3 transition-all rounded-b-xl">
      <div className="flex gap-12">
        {/* Logo */}  
        <Link href="/" className="flex items-center justify-start gap-2 text-xl font-bold">
            <img alt="Logo" width="48" height="28" decoding="async" data-nimg="1" src="/logo1.png"></img>
            <span className="hidden lg:block">PerFi</span>
        </Link>
        
         {/* Menú desktop */}
        <nav className="hidden sm:flex items-center justify-start gap-2 ">
            {navItems.map(({ href, label }) => (
                <Link
                    key={href}
                    href={href}
                    className="flex py-2 px-2 font-semibold text-text-light dark:text-muted-dark hover:text-primary dark:hover:text-primary-dark transition-colors"                                >
                    {label}
                </Link>
            ))}
        </nav>      
      </div>
      <div className="flex gap-2">
            {/* Botón cambio tema */}
            <button
            onClick={toggleTheme}
            aria-label="Cambiar tema claro/oscuro"
            className="flex items-center justify-end rounded-lg bg-primary px-3 py-1 text-white hover:bg-primary-dark transition"
            >
            {isDark ? <Sun size={18} className="mr-1" /> : <Moon size={18} className="mr-1" />}
            {isDark ? 'Claro' : 'Oscuro'}
            </button>

            <div className='mr-4'>
                
            </div>
            
            {/* Botón menú móvil */}
            <button
                onClick={() => setIsOpen(!isOpen)}
                aria-label="Abrir menú"
                className="ring-primary  rounded-lg px-3 py-[6px] focus:ring-2 sm:hidden"
            >
            {isOpen ? <X size={28} /> : <Menu size={28} />}
            </button>

        </div> 

      {/* Menú móvil desplegable */}
      {isOpen && (
        <nav className="fixed inset-0 z-40 flex flex-col bg-white dark:bg-background-dark p-6 pt-20 space-y-6 xl:hidden">
          {navItems.map(({ href, label }) => (
            <Link
              key={href}
              href={href}
              onClick={() => setIsOpen(false)}
              className="text-xl font-semibold text-text-light dark:text-text-dark hover:text-primary transition-colors"
            >
              {label}
            </Link>
          ))}
        </nav>
      )}
    </header>
  );
}
